.gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;

  @include mq(tablet) {
  gap: 12px;
  }
  @include mq(desktop) {
   gap: 16px;
  }
}

.сard {
  box-sizing: border-box;
  width: 280px;

  @include mq(tablet) {
   width: 336px;
  }
  @include mq(desktop) {
   width: 395px;
  }
  border-radius: $radius;
}

.сard__thumb {
  position: relative;

  width: 100%;
  overflow: hidden;
  
  transition: box-shadow $animation-timing-function;

  &:hover,
  &:focus {

    box-shadow: 0 0 20px 5px rgba(255, 107, 1, 0.1),
      0 0 12px 3px rgb(255, 107, 0.1);
  }
}

