.card__image {
  width: 100%;
  height: 398px;
  object-fit: contain;
  
  @include mq(tablet) {
    height: 455px;
  }
  @include mq(desktop) {
    height: 574px;
  }
}

.card__title {
  text-transform: uppercase;
  font-size: 12px;
  line-height: 1.33;
  padding-top: 8px;

  @include mq(desktop) {
    font-size: 20px;
    line-height: 1.2;
    padding-top: 10px;
  }
}

.card__caption {
  display: flex;
  align-items: center;
  // padding: 5px 0;
}

.card__info {
  font-size: 12px;
  line-height: 1.33;

  color: $accent-orange;

  @include mq(desktop) {
    font-size: 20px;
    line-height: 1.2;
  }
}

.card__rating {
  display: flex;
  justify-content: center;
  align-items: center;

  height: 16px;
  width: 36px;

  margin-left: 9px;
  border-radius: $radius;

  font-size: 12px;
  line-height: 1, 17;

  color: $main-white-color;
  background-color: $accent-orange;

  @include mq(tablet) {
    margin-left: 8px;
  }
  @include mq(desktop) {
    height: 20px;
    width: 40px;

    margin-left: 8px;
  }
}

